.sec-container-features {
    background: url('./images/padlock-background.png') center center/cover;
    background-position: left;
    min-height: 515px;
    font-family: 'Inter';
    /* vertical-align: center; */
    color: #fff;
}

.prod-features-list {
    margin-inline-start: 50px;
    margin-inline-start: 50px;
    margin-block: 0;
}

.sec-header {
    text-align: left;
    display: block;
    float: left;
    /* display: inline-block; */
    width: 300px;
}

.why-header {
    font-size: xxx-large;
    text-align: left;
    line-height: 48px;
    margin-block-start: 90px;
    margin-block-end: 0px;
}

.prod-features {
    letter-spacing: 1.5px;
    font-weight: 500;
    margin-block-start: 5px;
    margin-block-end: 40px;
}

.sec-prod-cards {
    display: flex;
}

/* .prod-cards-list {
    vertical-align: center;

} */

.prod-cards-rows {
    display: flex;
    float: right;
    list-style: none;
    margin-right: 100px;
    margin-top: 40px;
}

.prod-cards-rows-shift {
    display: flex;
    float: left;
    list-style: none;
    margin-right: 100px;
    margin-top: 40px;
}

.btn-sec {
    margin-right: 75px;
    float: right;
}

.more-button {
    background-color: var(--primary);
    color: var(--font);
    padding: 8px 20px;
    font-size: 1em;
    padding: 8px 20px;
    outline: none;
    border: none;
    cursor: pointer;
    font-family: 'Inter';
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.more-button:hover {
    background: transparent;
    border: 1px solid #2957CB;
}
